body {
  overflow: hidden;
}

.container {
  width: 100%;
  height: 95.9vh;
  padding-top: 30px;
}

.weather {
  width: 380px;
  padding: 5px;
  border-radius: 10px;
  margin: 50px auto;
  text-align: center;
  color: white;
}

.error {
  color: red;
  text-align: left;
  margin-right: 10px;
  font-size: 20px;
}

.time {
  font-size: 16px;
}

.title {
  margin-left: -45px;
  margin-top: -15px;
}

.logo1 {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  object-fit: cover;
}

.search {
  display: flex;
  align-items: center;
  justify-content: space-between;
  caret-color: blue;

}

.search button {
  border: none;
  width: 80px;
  outline: 0;
  height: 30px;
  border-radius: 10%;
  cursor: pointer;
  background-color: lightblue;
}

.search input {
  border: none;
  padding: 12px 25px;
  outline: 0;
  font-size: 18px;
  border-radius: 30px;
  flex: 1;
  margin-right: 12px;
}

.info h1 {
  font-size: 70px;
  font-weight: 500;
  margin-top: 5px;
  margin-left: -15px;
}

.info h2 {
  font-size: 40px;
  font-weight: 400;
  margin-top: -40px;
  margin-left: -25px;
}

.info .icon {
  width: 120px;
  margin-top: 20px;
  margin-left: -40px;
}

.details {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0, 20px;
  margin-top: 50px;
}

.details .col img {
  margin-bottom: 10px;
  width: 65px;
}

.details .col {
  display: flex;
  align-items: center;
  text-align: left;
}

.humidity, .wind {
  font-size: 25px;
  margin-top: -12px;
}

.humidity p, .wind p {
  margin: 5px;
  margin-left: 8px;
}

@media screen and (max-width: 768px) {
  .weather {
    width: 80%;
  }

  .search input {
    font-size: 16px;
  }

  .info h1 {
    font-size: 50px;
  }

  .info h2 {
    font-size: 30px;
  }

  .info .icon {
    width: 120px;
  }

  .humidity, .wind {
    font-size: 20px;
  }

  .details .col img {
    width: 45px;
  }
}